// NewsCarousel
.newsCarousel{
    width: 100%;
    height: 60vh;
    background-color: red;
}


// Artworks
.artworksContainer{
    width: 100%;
    height: 60vh;
    background-color: green;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    .art{
        background-color: pink;
        width: 33%;
        height: 55vh;
        margin: 1rem;
    }
}


// Game
.game{
    width: 100%;
    height: 100vh;
    background-color: blue;
}


@media only screen and (max-width: 600px) {

    .artworksContainer{

        flex-direction: column;
        height: auto;

    
        .art{

            width: 90%;
            height: 40vh;
        }
    }


    
}

